import React from 'react';
import {double} from 'helper/weightCalculation';
import {total} from 'helper/priceCalculation';
import PropTypes from 'prop-types';
import Customer from 'component/Customer';
export default class Shop extends React.Component {
  static propTypes = {
    customer: PropTypes.object.isRequired
  };
  static defaultProps = {
    companyName: 'Subway'
  };
  constructor (props) {
    super(props);

    this.state = {sandWichNo: 1};
  }
  get buggerSize () {
    return double(125);
  }
  onClick () {
    this.setState({sandWichNo: this.state.sandWichNo + 1});
  }
  render () {
    return (
      <article>
        <h1>
          Hi
          {' '}
          <Customer customer={this.props.customer} />
          , is
          {' '}
          {this.props.companyName}

        </h1>
        <em>Our double  meat chicken breast weight is {this.buggerSize} g.</em>
        <div onClick={this.onClick.bind(this)}>Click me to order</div>
        <div>Current Sandwich :{this.state.sandWichNo}</div>
        <div>
          Current Total(Per sandwich cost 100,discount is 80%) :
          {total(this.state.sandWichNo * 100, 0.8)}
        </div>
      </article>
    );
  }
}
